<template>
  <div class="pay">
    <van-nav-bar fixed title="訂單結算平台" left-arrow @click-left="$router.go(-1)" />

    <!-- 地址相關 -->
    <div class="address">

      <div class="left-icon">
        <van-icon name="logistics" />
      </div>

      <!-- 渲染地址數據 -->
      <div class="info" v-if="selectAddress">
        <div class="info-content">
          <span class="name">{{ selectAddress.name }}</span>
          <span class="mobile">{{ selectAddress.phone }}</span>
        </div>
        <div class="info-address">
          {{ longAddress }}
        </div>
      </div>
      <div class="info" v-else>
        <div class="info-content">
          <span class="name">小明</span>
          <span class="mobile">0912345678</span>
        </div>
        <div class="info-address">
          新北市新莊區
        </div>
      </div>

      <!-- <div class="info" v-else>
        請選擇配送地址
      </div> -->

      <div class="right-icon">
        <van-icon name="arrow" />
      </div>
    </div>

    <!-- 訂單明細 -->
<div class="pay-list" v-if="order.goodsList">
  <div class="list">
    <div class="goods-item" v-for="item in order.goodsList" :key="item.goods_id">
        <div class="left">
          <img :src="item.goods_image" alt="" />
        </div>
        <div class="right">
          <p class="tit text-ellipsis-2">
            {{ item.goods_name }}
          </p>
          <p class="info">
            <span class="count">x{{ item.total_num }}</span>
            <span class="price">${{ item.total_pay_price }}</span>
          </p>
        </div>
    </div>
  </div>

  <div class="flow-num-box">
    <span>共 {{ order.orderTotalNum }} 件商品，合計：</span>
    <span class="money">${{ order.orderTotalPrice }}</span>
  </div>

  <div class="pay-detail">
    <div class="pay-cell">
      <span>訂單總金額：</span>
      <span class="red">${{ order.orderTotalPrice }}</span>
    </div>

    <div class="pay-cell">
      <span>優惠券：</span>
      <span>無優惠券可用</span>
    </div>

    <div class="pay-cell">
      <span>配送費用：</span>
      <!-- <span v-if="selectAddress">請先選擇配送地址</span> -->
      <span class="red">+$0.00</span>
    </div>
  </div>

  <!-- 支付方式 -->
  <div class="pay-way">
    <span class="tit">支付方式</span>
    <div class="pay-cell">
      <span><van-icon name="balance-o" />餘額支付（可用 $ {{ personal.balance }} 元）</span>
      <!-- <span>請先選擇配送地址</span> -->
      <span class="red"><van-icon name="passed" /></span>
    </div>
  </div>

  <!-- 買家留言 -->
  <div class="buytips">
    <textarea v-model="remark" placeholder="選填:買家留言(50字內)" name="" id="" cols="30" rows="10"></textarea>
  </div>
</div>

<!-- 底部提交 -->
<div class="footer-fixed">
  <div class="left">實付款：<span>${{ order.orderTotalPrice }}</span></div>
  <div class="tipsbtn" @click="submitOrder">提交訂單</div>
</div>
  </div>
</template>

<script>
import { getAddressList, postAdd } from '@/api/address.js'
import { checkOrder, submitOrder } from '@/api/order'
export default {
  name: 'PayIndex',
  data () {
    return {
      // 地址列表
      addressList: [],
      order: {}, // 訂單訊息
      personal: {}, // 用戶餘額
      remark: '', // 買家留言
      form: {
        name: '张小二',
        phone: '18999292929',
        region: [
          {
            value: 782,
            label: '上海'
          },
          {
            value: 783,
            label: '上海市'
          },
          {
            value: 785,
            label: '徐汇区'
          }
        ],
        detail: '北京路1号楼8888室'
      } // 添加地址
    }
  },
  computed: {
    // 默認獲取地址列表的第一條地址
    selectAddress () {
      return this.addressList[0]
    },
    // 渲染出實際地址字串_將第一條地址的數據進行拼接
    longAddress () {
      const region = this.selectAddress.region
      return region.province + region.city + region.region + this.selectAddress.detail
    },
    // 來自哪個頁面的請求
    mode () {
      return this.$route.query.mode
    },
    // 購物車商品id
    cartIds () {
      return this.$route.query.cartIds
    },
    // 來自商品詳情頁的立刻購買傳參
    goodsId () {
      return this.$route.query.goodsId
    },
    // 來自商品詳情頁的立刻購買傳參
    goodsSkuId () {
      return this.$route.query.goodsSkuId
    },
    // 來自商品詳情頁的立刻購買傳參
    goodsNum () {
      return this.$route.query.goodsNum
    }
  },
  async created () {
    // 初始化地址列表
    this.getAddressList()
    // 初始化訂單頁面的數據
    this.getOrderList()
    // 添加地址
    this.gopostAdd()
  },
  methods: {
    // 調用地址接口
    async getAddressList () {
      const { data: { list } } = await getAddressList()
      this.addressList = list
    },
    // 調訂單頁面接口獲取數據
    async getOrderList () {
      // 來自購物車的參數
      if (this.mode === 'cart') {
        // 第二個傳參是obj所以要傳對象
        const { data: { order, personal } } = await checkOrder(this.mode, { cartIds: this.cartIds })
        this.order = order
        this.personal = personal
      }
      // 來自商品型情頁的參數
      if (this.mode === 'buyNow') {
        // 第二個傳參是obj所以要傳對象
        const { data: { order, personal } } = await checkOrder(this.mode, {
          goodsId: this.goodsId,
          goodsSkuId: this.goodsSkuId,
          goodsNum: this.goodsNum
        })
        this.order = order
        this.personal = personal
      }
    },
    // 提交訂單
    async submitOrder () {
      if (this.mode === 'cart') {
        await submitOrder(this.mode, {
          remark: this.remark,
          cartIds: this.cartIds
        })
      }
      if (this.mode === 'buyNow') {
        await submitOrder(this.mode, {
          remark: this.remark,
          goodsId: this.goodsId,
          goodsSkuId: this.goodsSkuId,
          goodsNum: this.goodsNum
        })
      }
      this.$toast.success('支付成功')
      this.$router.replace('/myorder')
    },
    // 添加地址
    async gopostAdd () {
      await postAdd(this.form)
    }
  }
}
</script>

<style lang="less" scoped>
.pay {
  padding-top: 46px;
  padding-bottom: 46px;
  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}
.address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  font-size: 14px;
  color: #666;
  position: relative;
  background: url(@/assets/logo.png) bottom repeat-x;
  background-size: 30px 3px;
  .left-icon {
    margin-right: 20px;
  }
  .right-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-7px);
  }
}
.goods-item {
  height: 100px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;
  .left {
    width: 100px;
    img {
      display: block;
      width: 80px;
      margin: 10px auto;
    }
  }
  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;
    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;
      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px;
  font-size: 14px;
  border-bottom: 1px solid #efefef;
  .money {
    color: #fa2209;
  }
}

.pay-cell {
  font-size: 14px;
  padding: 10px 12px;
  color: #333;
  display: flex;
  justify-content: space-between;
  .red {
    color: #fa2209;
  }
}
.pay-detail {
  border-bottom: 1px solid #efefef;
}

.pay-way {
  font-size: 14px;
  padding: 10px 12px;
  border-bottom: 1px solid #efefef;
  color: #333;
  .tit {
    line-height: 30px;
  }
  .pay-cell {
    padding: 10px 0;
  }
  .van-icon {
    font-size: 20px;
    margin-right: 5px;
  }
}

.buytips {
  display: block;
  textarea {
    display: block;
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer-fixed {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  border-top: 1px solid #efefef;
  font-size: 14px;
  display: flex;
  .left {
    flex: 1;
    padding-left: 12px;
    color: #666;
    span {
      color:#fa2209;
    }
  }
  .tipsbtn {
    width: 121px;
    background: linear-gradient(90deg,#f9211c,#ff6335);
    color: #fff;
    text-align: center;
    line-height: 46px;
    display: block;
    font-size: 14px;
  }
}
</style>
